@import "variables";

@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    font-size: 16px;
    line-height: 18px;
    .content {
      .container {
        .content__actualProduct,
        .content__saleProduct {
          &__slider{
            img {
              height: 330px;
            }
            .slider {
              width: 950px;
              height: 330px;
              .lSSlideOuter {
                ul {
                  .item-a,
                  .item-b,
                  .item-c,
                  .item-e,
                  .item-d,
                  .item-f {
                    width: 235px;
                    height: 330px;
                  }
                }
              }
            }
          }
        }
        .content__howToMakeOrder {
          div {
            .info {
              margin-left: 55px;
            }
          }
        }
      }
    }
    .contentAddres {
      iframe {
        height: 465px;
      }
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 184px;
          }
          textarea {
            width: 395px;
          }
        }
        .contacts,
        .social{
          width: 75%;
        }
      }
    }
  }
}

