@media screen and (max-width: 1050px), screen and (max-device-width: 1050px){
  body{
    .content{
      .container{
        .content__actualProduct,
        .content__saleProduct{
          &__headline{
            div{
              margin: 20px 40px 0 0;
              flex-direction: column;
              justify-content: flex-end;
              p{
                margin: 5px 0;
                font-size: 1em;
                line-height: 1.125em;
              }
            }
          }
          &__slider{
            img{
              width: 150px;
              height: 270px;
            }
            .slider{
              width: 650px;
              height: 270px;
              .lSSlideOuter {
                ul {
                  .item-a,
                  .item-b,
                  .item-c,
                  .item-e,
                  .item-d,
                  .item-f {
                    width: 235px;
                    height: 270px;
                  }
                  li {
                    a{
                      img{
                        width: 150px;
                        height: 150px;
                      }
                      p{
                        font-size: 1.25em;
                        line-height: 1.375em;
                      }
                    }
                  }
                }
              }
            }
          }
        }
        .content__howToMakeOrder{
          div{
            height: 150px;
            .icons{
              .arrow{
                margin: 15px 0 0 15px;
              }
              .cart,
              .box,
              .truck{
                margin-top: 20px;
                width: 70px;
                height: 70px;
              }
            }
            .learnMore{
              a{
                width: 125px;
              }
            }
          }
        }
      }
    }
  }
}

